<template>
	<view>
		<view class="status_bar"></view>
		<Head :headTitle="headTitle" />
		<view class="loginBox">
			<view class="PhoneBox">
				<text :class="[{'icon-actived':phoneStatus},'iconfont icon-shouji']"></text>
				<input :class="[{'actived':phoneStatus},'phoneInput']" type="number" v-model="phoneNumber" maxlength="11"
				 placeholder="请输入手机号" placeholder-class="phonePClass" />
			</view>
			<view class="passBox">
				<view class="passBox_">
					<text :class="[{'icon-actived':verifyStatus},'iconfont icon-mima']"></text>
					<input :class="[{'actived':phoneStatus},'passInput']" type="number" v-model="verifyNumber" maxlength="6"
					 placeholder="请输入收到的短信验证码" placeholder-class="passPClass" />
				</view>
				<text @click="verifyBtn" :class="[{'actived-verifyText':phoneStatus},'verifyText']">{{verifyText}}</text>
			</view>
		</view>
		<button :class="[{'actived-loginBtn':phoneStatus},'loginBtn']" @click="loginBtn">登录</button>
	</view>
</template>

<script>
	import {
		httpRequest_,
		otherAPI
	} from 'pages/common/API.js';
	import Head from '../common/head.vue'
	export default {
		components: {
			Head
		},
		data() {
			return {
				// head
				headTitle: {
					title: '登录',
					color: '#FFF',
					size: '35.93rpx'
				},
				phoneNumber: '',
				verifyNumber: '',
				verifyText: '获取验证码',
				verifyDisabled:false
			}
		},
		onReady() {
			this.phoneNumber = uni.getStorageSync('phone') ? uni.getStorageSync('phone') : ''
		},
		computed: {
			phoneStatus() {
				if (this.phoneNumber.length > 0) {
					return true
				} else {
					return false
				}
			},
			verifyStatus() {
				if (this.verifyNumber.length > 0) {
					return true
				} else {
					return false
				}
			}
		},
		methods:{
			// 发送验证码
			verifyBtn() {
				if(!otherAPI.verifyPhone(this.phoneNumber)) {
					otherAPI.toast('手机号格式不正确', 'none', false, 1500);
					return false;
				}
				if (this.verifyDisabled) {
					return false;
				}
				let timer = ''
				let count = 60;
				let url = `/api/user/sendCode`;
				let obj = {
					mobile: this.phoneNumber
				}
				httpRequest_.post(url, obj).then(res => {
					if (res.code != 1) {
						otherAPI.toast(res.msg, 'none', false, 1500);
					} else {
						otherAPI.toast(res.msg, 'none', false, 1500);
						timer = setInterval(() => {
							this.verifyDisabled = true;
							this.verifyText = count + "秒";
							count--;
							if (count < 0) {
								this.verifyText = "重新发送"
								clearInterval(timer);
								this.verifyDisabled = false;
							}
						}, 1000);
					}
				});
			},
			// 登录
			loginBtn() {
				if(!otherAPI.verifyPhone(this.phoneNumber)) {
					otherAPI.toast('手机号格式不正确', 'none', false, 1500);
					return false;
				}
				if(this.verifyNumber.length < 6) {
					otherAPI.toast('请输入正确验证码', 'none', false, 1500);
					return false;
				}
                //优化验证码[手机号有正则,不用优化],因为目前来看,输入点的时候,也能输入            
                //验证码转换成数组
                const newVerifyNumber=this.verifyNumber.split('');               
                const verifyNumberIndex=newVerifyNumber.findIndex(v=>v=='.')
                if(verifyNumberIndex!==-1){
                    otherAPI.toast('请输入正确验证码', 'none', false, 1500);
                    return false;
                }             
               //到这,优化验证码结束 
                                          
				let url = `/api/user/login`;
				let obj = {
					mobile: this.phoneNumber,
					code:this.verifyNumber
				}
				httpRequest_.post(url, obj).then(res => {
					if (res.code != 1) {
						otherAPI.toast(res.msg, 'none', false, 1500);
					} else {                
						uni.setStorageSync('data', res.data)  
						uni.redirectTo({
							url: '/pages/index/index'
						});
					}
				});
			}
		}
	}
</script>

<style>
	page {
		width: 100%;
		height: 100%;
		background-image: url(../../static/login.png) !important;
		background-size: 100% 100%;
	}

	.status_bar {
		height: var(--status-bar-height);
		width: 100%;
	}

	.loginBox {
		width: 625rpx;
		height: 250rpx;
		margin: 166.66rpx auto 0;
	}

	.passInput,
	.phoneInput {
		display: inline-block;
		width: 350rpx;
		height: 89.44rpx;
		margin-left: 34.72rpx;
	}

	.PhoneBox,
	.passBox {
		height: 125rpx;
		display: flex;
		align-items: center;
		border-bottom: 0.69rpx solid #13234c;
		color: #4c5c85;
		font-size: 31.94rpx;
	}

	.passBox {
		justify-content: space-between;
	}

	.passBox_ {
		display: flex;
		align-items: center;
	}

	.phonePClass,
	.passPClass {
		color: #4c5c85;
		font-size: 31.94rpx;
	}

	.verifyText {
		color: #12254c;
	}

	.icon-shouji,
	.icon-mima {
		color: #081838;
		font-size: 38.19rpx;
	}

	.actived {
		color: #eaf1ff;
	}

	.icon-actived,.actived-verifyText {
		color: #227eea;
	}

	.loginBtn {
		margin-top: 55.55rpx;
		width: 625rpx;
		line-height: 100.69rpx;
		color: #4c5c85;
		font-size: 31.94rpx;
		font-weight: bold;
		background-color: #081838;
	}
	
	.actived-loginBtn {
		color: #eaf1ff;
		background-color: #22376b;
	}
</style>
